var sourceType = [
		['camera'], //拍照
		['album'], //相册
		['camera', 'album'] //拍照或相册
	]
	var sizeType = [
		['compressed'], //压缩
		['original'], //原图
		['compressed', 'original'] //压缩或原图
	]
export default{
	data(){
		return{
			goods:[],
			current:1,
			cateCurrent:0,
			cateCurrent2:0,
			cateCurrent3:0,
			 array: ['待发货', '待收货', '已收货'],
			 array2: ['质量不好', '欺诈', '无理取闹', '辣鸡'],
			 array3: ['快递', '快递2'],
			imageList: [], //保存图片路径集合
			imageLength: 5, //限制图片张数
			sourceTypeIndex: 2, //添加方式限制
			sizeTypeIndex: 2, //图片尺寸限制
			// 地址
			lat:'',
			lng:'',
			pic:[],
			address:'',
			form:{
				price:'',
				uid:'',
				token:'',
				reason:'',
				orderno:'',
				goods:[],
				ways:'',
				remark:'',
				type:'',
				pics:''
			}
		}
	},
	onLoad(p){
		this.goods=JSON.parse(p.goods)
		this.form.type=p.type
		this.form.orderno=p.orderno
		var userInfo=uni.getStorageSync('userInfo'),that=this
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.form.uid=userInfo.uid
			this.form.token=userInfo.token
		}
	},
	methods:{
		to(w){
			uni.navigateTo({
				url:`/pages/drawback/afterSale`
			})
		},
		//选择图片
		chooseImage: async function() {
				uni.chooseImage({
					sourceType: sourceType[this.sourceTypeIndex],

					sizeType: sizeType[this.sizeTypeIndex],

					count: this.imageLength - this.imageList.length,
					success: (res) => {
						this.imageList = this.imageList.concat(res.tempFilePaths);
					}
				})
			},
			//预览图片
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			//删除图片
			deleteImage: function(e) {
				var index = e.target.dataset.index;
				var that = this;
				var images = that.imageList;
				images.splice(index, 1);
				that.imageList = images;
			},
			bindPickerChange(e){
				this.cateCurrent=e.detail.value
			},
			bindPickerChange2(e){
				this.cateCurrent2=e.detail.value
				this.form.reason=this.array2[this.cateCurrent2]
			},
			bindPickerChange3(e){
				this.cateCurrent3=e.detail.value
				this.form.ways=this.array3[this.cateCurrent3]
			},
			filter(event) { //过滤input密码类型只输入数字
					let i = Number(event.target.value)
					// var reg=new RegExp("^[0-9]*$")
					if(i>45) {
						setTimeout(() => {
							this.form.price=45
						},0)
					} else if(i<0){
						setTimeout(() => {
							this.form.price=0
						},0)
					}
				},
				submit(){
					var  that=this,l=this.imageList.length;
					this.$loading()
					if(l==0){
						that.submitForm()
					}else{
						this.imageList.forEach((item,index) =>{
							that.$upload(item,'',(cb) =>{
								that.pic.push(cb)
							var	t=this.pic.length
								if(l==t){
									that.submitForm()
								}
							})
						})
					}
					
				},
				submitForm(){
					var that=this
					var url='&r=api.member.order.refund'
					this.goods.forEach((item) =>{
						this.form.goods.push({
							goodsid:item.goodsid,
							amount:item.amount
						})
					})
					this.form.pics=this.pic.join(',')
					  this.$apiPost(url,this.form).then((res) =>{
							that.$msg('评价成功')
							that.pic=[]
							uni.hideLoading()
							// setTimeout(function(){
							// 	that.to()
							// },500)
					  })
				}
	}
}
